import { ReactElement, useState } from 'react';

const Smart = (): ReactElement => {
  const [numDiners, setNumDiners] = useState(3);
  const [isEveryAbove12FullyVaccinatedOrEligible, setIsEveryAbove12FullyVaccinatedOrEligible] = useState(true);
  // child def as 12 or below
  const [hasAnyChildren, setHasAnyChildren] = useState(true);
  const [areDinersSameHouseHold, setAreDinersSameHousehold] = useState(true);
  const [numChildren, setNumChildren] = useState(3);

  const canDineIn = () => {
    if (numDiners <= 2) {
      return true;
    }
    if (numDiners >= 6) {
      return false;
    }
    if (!isEveryAbove12FullyVaccinatedOrEligible) {
      return false;
    }
    if (numChildren === 0) {
      return true;
    }
    if (areDinersSameHouseHold) {
      if (numChildren <= 4) {
        return true;
      }
      return false;
    }
    if (numChildren === 1) {
      return true;
    } else if (numChildren === 2) {
      if (numDiners === 3) {
        return false;
      } else if (numDiners === 4 || numDiners === 5) {
        return true;
      }
    }
  }

  return (
    <div>
      <h1>Smart Mode</h1>
      <h1>
        Can you Dine-in Together? {canDineIn() ? 'Can dine in together' : 'Cannot. Sad'}
      </h1>
      <div>Section under construction</div>
    </div>
  );
}

export default Smart;
