import { ReactElement, useState } from 'react';
import child from './images/child.png';
import adult from './images/adult.png';
import legend from './images/legend.png';
import can from './images/can.png';
import cannot from './images/cannot.png';
import fullyVaccinated from './images/fullyVaccinated.png';
import partiallyVaccinated from './images/partiallyVaccinated.png';
import notVaccinated from './images/notVaccinated.png';
import recovered from './images/recovered.png';
import negativeTest from './images/negativeTest.png'

interface Person {
  isChild: boolean;
  isPartiallyVaccinated: boolean;
  isFullyVaccinated: boolean;
  isRecovered: boolean;
  isNegativeTest: boolean;
}

const Stupid = (): ReactElement => {
  const personDefault = {
    isChild: false,
    isPartiallyVaccinated: false,
    isFullyVaccinated: false,
    isRecovered: false,
    isNegativeTest: false
  };
  const [people, setPeople] = useState<Array<Person>>([personDefault, personDefault])

  const addPerson = () => {
    const newPeople = people.concat(personDefault);
    setPeople(newPeople);
  }

  const removePerson = () => {
    if (people.length === 2) return;
    const newPeople = people.slice(0, -1);
    setPeople(newPeople);
  }

  const numDiners = people.length
  const isEveryAbove12FullyVaccinatedOrEligible = people.every(person => {
    return person.isFullyVaccinated ||
      person.isNegativeTest ||
      person.isRecovered
  });
  // child def as 12 or below
  const numChildren = people.filter(p => p.isChild === true).length;
  const [areDinersSameHouseHold, setAreDinersSameHousehold] = useState(true);

  const canDineIn = () => {
    if (numDiners <= 2) {
      return true;
    }
    if (numDiners >= 6) {
      return false;
    }
    if (!isEveryAbove12FullyVaccinatedOrEligible) {
      return false;
    }
    if (numChildren === 0) {
      return true;
    }
    if (areDinersSameHouseHold) {
      if (numChildren <= 4) {
        return true;
      }
      return false;
    }
    if (numChildren === 1) {
      return true;
    } else if (numChildren === 2) {
      if (numDiners === 3) {
        return false;
      } else if (numDiners === 4 || numDiners === 5) {
        return true;
      }
    }
  }

  const renderPersonImg = (person: Person, index: number) => {
    const { isChild, isFullyVaccinated, isPartiallyVaccinated, isNegativeTest, isRecovered } = person;
    return (
      <div key={index} className="person">
        <div className="status-icons">
          {!(isFullyVaccinated || isPartiallyVaccinated || isNegativeTest || isRecovered) && <img src={notVaccinated} />}
          {isPartiallyVaccinated && <img src={partiallyVaccinated} />}
          {isFullyVaccinated && <img src={fullyVaccinated} />}
          {isNegativeTest && <img src={negativeTest} />}
          {isRecovered && <img src={recovered} />}
        </div>
        <div className="human">
          {isChild ? <img className="child" src={child} /> : <img className="adult" src={adult} />}
        </div>
      </div>
    );
  }

  const renderPerson = (person: Person, index: number) => {
    return (
      <div key={index}>
        <h2>Person {index + 1} </h2>
        <input type="checkbox" id="isChild" name="isChild"
          checked={person.isChild}
          onChange={() => {
            const newPeople = people.map((p, i) => {
              if (i === index) {
                const updatedPerson = {
                  ...p,
                  isChild: !p.isChild
                }
                return updatedPerson;
              }
              return p;
            })
            setPeople(newPeople);
          }}></input>
        <label htmlFor="isChild">Is a Child. 12 or Below.</label>
        <br></br>
        <input type="checkbox" id="isFullyVaccinatedOrEligible" name="isFullyVaccinatedOrEligible"
          checked={person.isFullyVaccinated}
          onChange={() => {
            const newPeople = people.map((p, i) => {
              if (i === index) {
                const updatedPerson = {
                  ...p,
                  isFullyVaccinated: !p.isFullyVaccinated
                }
                return updatedPerson;
              }
              return p;
            })
            setPeople(newPeople);
          }}></input>
        <label htmlFor="isFullyVaccinatedOrEligible">Is Fully Vaccinated (including 2 weeks wait)</label>
        <br></br>
        <input type="checkbox" id="isPartiallyVaccinated" name="isPartiallyVaccinated"
          checked={person.isPartiallyVaccinated}
          onChange={() => {
            const newPeople = people.map((p, i) => {
              if (i === index) {
                const updatedPerson = {
                  ...p,
                  isPartiallyVaccinated: !p.isPartiallyVaccinated
                }
                return updatedPerson;
              }
              return p;
            })
            setPeople(newPeople);
          }}></input>
        <label htmlFor="isPartiallyVaccinated">Is Partially Vaccinated (this is a useless irrelevant option lmao)</label>
        <br></br>
        <input type="checkbox" id="recovered" name="recovered"
          checked={person.isRecovered}
          onChange={() => {
            const newPeople = people.map((p, i) => {
              if (i === index) {
                const updatedPerson = {
                  ...p,
                  isRecovered: !p.isRecovered
                }
                return updatedPerson;
              }
              return p;
            })
            setPeople(newPeople);
          }}></input>
        <label htmlFor="recovered">Recovered from Covid-19 in the past 270 days</label>
        <br></br>
        <input type="checkbox" id="testNegative" name="testNegative"
          checked={person.isNegativeTest}
          onChange={() => {
            const newPeople = people.map((p, i) => {
              if (i === index) {
                const updatedPerson = {
                  ...p,
                  isNegativeTest: !p.isNegativeTest
                }
                return updatedPerson;
              }
              return p;
            })
            setPeople(newPeople);
          }}></input>
        <label htmlFor="testNegative">Negative pre-event test result</label>
      </div >
    );
  }

  return (
    <div>
      <h1>ST Mode</h1>
      <h1>
        {'Can you Dine-in Together? '}
        {canDineIn() ? <img src={can} /> : <img src={cannot} />}
        {canDineIn() ? 'Can dine in together' : 'Cannot. Sad'}
      </h1>
      <img className="legend" src={legend} />
      <div className="people">
        {people.map(renderPersonImg)}
      </div>
      <button onClick={addPerson}>Add person</button>
      {people.length > 2 && <button onClick={removePerson}>Remove person</button>}
      {people.map(renderPerson)}
      <div>
        <br></br>
        <input type="checkbox" id="sameHousehold" name="sameHousehold"
          checked={areDinersSameHouseHold}
          onChange={() => { setAreDinersSameHousehold(!areDinersSameHouseHold) }}></input>
        <label htmlFor="sameHousehold">Same household?</label>
      </div>
    </div>
  );
}

export default Stupid;
